function adder(n){
  if(n<0.1){
    return 0.005;
  }
  if(n<1){
    return 0.1;
  }
  else{
    return 0;
  }
}

//sets domains: 
y.domain([0, ((d3.max(data)+adder(d3.max(data))).toFixed(2))]); 
x.domain([350,800]); 

var line = d3.line()
    .x(function(d, i) { return x(i)+257; }) // set the x values for the line generator +257
    .y(function(d) { return y(d); }) // set the y values for the line generator 
    .curve(d3.curveMonotoneX) // apply smoothing to the line


